//使用数组构造路由
import Home from './Home';
import Files from './Files';
import Course from './Course';
import Blog from './Blog';
import Com from './Com';
import Login from './Login';
import Core from './Core';
import Guide from './Guide';
import Api from './Api';
import Hooks from './Hooks';

const routes = [
    {
        path:'/',
        exact: true,
        component:Home,
        pri:false
    },
    {
        path:'/files',
        component:Files,
        pri:true,
        routes:[
            {
                path:'/files/core',
                component:Core,
            },
            {
                path:'/files/guide',
                component:Guide,
            },
            {
                path:'/files/api',
                component:Api,
            },
            {
                path:'/files/hooks',
                component:Hooks,
            }
        ]
    },
    {
        path:'/course',
        component:Course,
        pri:true
    },
    {
        path:'/blog',
        component:Blog,
        pri:false
    },
    {
        path:'/com',
        component:Com,
        pri:false
    },
    {
        path:'/login',
        component:Login,
    }
]

export default routes;