import { TabBar } from 'antd-mobile';
import React from 'react'

class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'redTab',
      hidden: false
    };
  }


  render() {
    return (
      <div style={{ position: 'fixed',  width: '100%', bottom: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="orange"
          barTintColor="white"
          hidden={this.state.hidden}
        >
          <TabBar.Item
            title="首页"
            key="home"
            icon={
              <i className='iconfont icon-shouye'></i>
            }
            selectedIcon={
              <i className='iconfont icon-icon_huabanfuben'></i>
            }
            selected={this.state.selectedTab === 'blueTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'blueTab',
              });
            }}
            data-seed="logId"
          >
            店铺
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-gouwuche1'></i>
            }
            selectedIcon={
              <i className='iconfont icon-gouwuche'></i>
            }
            title="购物车"
            key="shopcar"
            selected={this.state.selectedTab === 'redTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'redTab',
              });
            }}
            data-seed="logId1"
          >
            客服
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-dingdanliebiao'></i>
            }
            selectedIcon={
              <i className='iconfont icon-dingdanliebiao-tianchong'></i>
            }
            title="订单列表"
            key="shoplist"
            selected={this.state.selectedTab === 'greenTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'greenTab',
              });
            }}
          >
            收藏
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-wode'></i>
              }
            selectedIcon={
              <i className='iconfont icon-wodedangxuan'></i>
            }
            title="我的淘宝"
            key="myTaobao"
            selected={this.state.selectedTab === 'yellowTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'yellowTab',
              });
            }}
          >
            加入购物车
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-gengduo'></i>
            }
            selectedIcon={
              <i className='iconfont icon-19'></i>
            }
            title="更多"
            key="more"
            selected={this.state.selectedTab === 'reddTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'reddTab',
              });
            }}
          >
            立即购买
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default Mytab
