import React from 'react';
import {Route,Redirect} from 'react-router-dom';

const RouteWithSubRoutes = (route) => {//要是放在App.js中，File.js就无法加载，为什么？
    if(route.pri){
        return <PrivateRoute {...route}/>
    }else{
        return (
            <Route
            path={route.path}
            render={props => (
                <route.component {...props} routes={route.routes} />
            )}
            />
        );
    }
}

function PrivateRoute({ component:Com,routes:rou, ...rest }) {
    //组件必须大写。传参时需重命名
    return (
      <Route
        {...rest}
        render={({ location }) =>
           localStorage.getItem('userinfo')
           ? <Com {...rest} routes={rou}/>
           : (
            <Redirect
              to={{
                pathname: "/login",
                state: location 
              }}
            />
          )
        }
      />
    );
  }

export default RouteWithSubRoutes;