import React ,{useEffect}from 'react'
import {BrowserRouter as Router,NavLink,Redirect,
     Switch,
    } from 'react-router-dom'
import './Files.css';
import RouteWithSubRoutes from './RouteWithSubRoutes';

const Files = (props) => {
    return (
        <div className='doc'> 
            <div className='content'>
                <Switch>
                {
                    props.routes.map((route,i)=>{
                        return<RouteWithSubRoutes key={i} {...route}/>
                    })
                }
                </Switch>
            </div>
            <ol>
                <li><NavLink to='/files/core'>核心概念</NavLink></li>
                <li><NavLink to='/files/guide'>高级指引</NavLink></li>
                <li><NavLink to='/files/api'>API</NavLink></li>
                <li><NavLink to='/files/hooks'>Hooks</NavLink></li>
            </ol>
            
            {/* <div className="content">
                
                嵌套路由 */}
                {/* <Route path='/files/:type' component={DocContent}/>
            </div> */}   
        </div>
    )
}

export default Files;

